* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100vw;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body {
  color: white;
  user-select: none;
}

.backgroundDiv {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgb(29,26,46);
  background: radial-gradient(circle, rgb(21, 19, 29) 0%, rgb(19, 18, 24) 100%);  
}

.backgroundDiv:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 90%;
  overflow: visible;
  border-radius: 50%;
  border-radius: 50%;
  background: #247dc6;
  background: radial-gradient(circle, rgba(63, 208, 252, 0.85) 7%, rgba(63, 208, 252, 0.7) 20%, rgba(63, 208, 252, 0.62) 32%, rgba(63, 208, 252, 0.5) 35%, rgba(63, 208, 252, 0.4) 45%, rgba(63, 208, 252, 0.15) 55%, rgba(63, 208, 252, 0.05) 65%, rgba(63, 208, 252, 0) 70%);
  filter: blur(30px);
  opacity: 0.5;
}

.videoTag {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  transition: 3s ease-in-out;
}

.videoTag.hide {
  opacity: 0;
}